.boards-header-container{
    width: 100%;
    // height: 4vh;
    max-height: rem(50px);
    display: flex;
    align-items: center;
    z-index: 4;
    background-color: rgba(0,0,0, .05);
    padding: 0 0.5rem;

    >*{
        margin-left: rem(4px) ;
        margin-right: 0.5rem;

    }
    
    &>h3{
        font-size: rem(18px);
        color: $textLightClr;
    }   
}

.members-container{
    height: 100%;
    display: flex;
    align-items: center;
}

.member-preview{
    background-color: $memberClr;
    color:$textClr;
    height: rem(28px);
    width: rem(28px);
    border-radius: 50%;
    font-family: Roboto-Bold;
    font-size: rem(14px);
    text-align: center;
    line-height: rem(28px);
    cursor: pointer;
    transition: 0.1s;
    
    &:hover{
        background-color: $memberClrHovered;
    }
}

// filter

.search-filter-container{
    align-items: center;
    justify-content: flex-start;
    display: flex;
    flex-grow: 1;

    &>*{
        align-self: center;
        justify-self: center;
    }

}


.search-bar{
        color: #fff;
        font-size: rem(16px);
        background-color: hsla(0, 0%, 100%, .3);
        border-radius: rem(3px);
        border: none;
        box-shadow: none;
        color: $textClr;
        height: rem(36px);
        margin: 0;
        outline: none;
        padding-left: rem(8px);
        padding-right: rem(30px);
        transition: .13s;
        width: rem(180px);
 }
 
 .input-container {
     display: flex;
     transition: 0.2s;


     &>.search-icon-container {
         transform: translate(-120%, 10%);
         color: #fff;
     }

     input:focus+.search-icon-container {
         color: #464646
     }

     input:focus {
         box-shadow: $modalShadow;
         background-color: rgba(255, 255, 255, 0.95);
         width: rem(260px);
         font-size: rem(16px);
     }
 }


// buttons

.board-header-btn{
    background-color: $buttonBgColor;
    cursor: pointer;
    color: #fff;
    border-radius: 0.3em;
    line-height: 0.75;
    font-size: rem(14px);
    font-family: Roboto-Regular;
    letter-spacing: 0.025em;
    padding: 0 rem(12px);
    transition: 0.1s;

    &:hover{
        background-color: $buttonBgColorHovered;
    }

    &>span {
        justify-self: center;
        align-self: center;
        text-align: center;
        min-width: 20px;
        max-width: 30px;
        border-radius: 0.3em;
        line-height: 1.5;
        letter-spacing: 0.025em;
    }
}

.filter-btn{
        transform: translateX(-36%);
}

.header-logo{
    display: flex;
    width: 36px;
    align-items: center;
}